* {
  padding: 0;
  margin: 0;
}

.root {
  background-color: black;
  height: 100vh;
  display: flex;
  flex-direction: column;
  /* animation: mymove 2s infinite; */
}

.date {
  text-align: center;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: space-evenly;
  border-bottom: solid 1px white;
  color: white;
  margin-bottom: 10px;
}

h1 {
  font-weight: 200;
  padding: 5px;
  font-size: 5em;
}

h2 {
  font-size: 2em;
  font-weight: 100;
}

.lyrics {
  height: 55%;
  margin: 30px;
  display: grid;
  place-items: center;
  border: dashed 1px white;
}

.timerBig {
  text-align: center;
  height: 100%;
  padding-bottom: 5px;
  grid-column: 1/4;
  grid-row: 2;
  font-weight: 900;
  color: white;
}

.timerBig h1 {
  font-size: 22em;
}

.timersWrapper {
  height: 25%;
  text-align: center;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  border-top: solid 1px white;
  color: white;
}

.timer {
  height: 100%;
  padding-bottom: 5px;
  grid-column: 2/4;
  grid-row: 1;
  font-weight: 900;
  color: white;
}

.timer h1 {
  font-size: 11em;
}

.capture {
  padding-bottom: 5px;
  align-self: flex-end;
  grid-column: 1;
  grid-row: 1;
  font-weight: 900;
  color: white;
}

.capture h1 {
  text-align: center;
  font-size: 4em;
}
.capture h2 {
  text-align: center;
  font-weight: 290;
}

@media screen and (max-width: 700px) {
  .date {
    margin-bottom: 4px !important;
  }
  .date h1 {
    font-size: 10px !important;
  }

  h1 {
    font-size: 10px !important;
  }

  h2 {
    font-size: 10px !important;
  }

  .timer {
    padding-top: 5px !important;
  }
  .timer h1 {
    font-size: 5px;
  }
  .lyrics {
    padding-top: 5px;
    margin: 2px;
    height: 36%;
  }
  .lyrics h1 {
    font-size: 30px !important;
  }
  .capture {
    place-self: center;
    padding: 0;
  }

  .capture h1 {
    text-align: center;
    font-size: 10px !important;
  }
  .capture h2 {
    text-align: center;
    font-weight: 290;
    font-size: 9px !important;
  }
}

@keyframes mymove {
  0% {
    background-color: red;
  }
  100% {
    background-color: rgb(0, 0, 0);
  }
}
